import { Typography, Grid, Button, TextField, FormControl, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core'
import { Container, Circle, AboutContainer, useStyles } from './Styles'
import React, { useState } from 'react'
import about from '../Assets/Power-OM.jpg'
import Footer from './Footer'

export default function About() {

    const [open, setOpen] = useState(false);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [number, setNumber] = useState('');
    const [message, setMessage] = useState('');

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };


    const handleNameChange = (event) => {
        setName(event.target.value)
    };

    const handleEmailChange = (event) => {
        setEmail(event.target.value);
    };

    const handleNumberChange = (event) => {
        setNumber(event.target.value)
    };

    const handleMessageChange = (event) => {
        setMessage(event.target.value)
    };

    const handleSubmit = () => {
        const info = {
            'Name': name,
            'Email': email,
            'Number': number,
            'Message': message
        }

        console.log(info)
    }

    return (
        <Container>

            <Grid container alignItems='center'>

                <Grid item xs={12}>
                    <Typography variant='h3' align='center' style={{ fontFamily: 'Montserrat', color: '#4B4C5D', margin: '5rem', fontWeight: '600' }}>
                        About Us
                    </Typography>
                </Grid>

                <Grid item xs={6}>
                    <Typography variant='body1' align='left' style={{ fontFamily: 'Montserrat', color: '#4B4C5D', margin: '3rem' }}>
                        Associated Power Systems are channel associates of Thermax Global, Escoltrix and Growth Associates in the field of Engineering, Procurement
                        and Construction (EPC) of Solar, Process Cooling and Electrical and Lightning Protection Systems that provides a single point of contact
                        for your energy needs.

                    </Typography>
                    <Typography variant='body1' align='left' style={{ fontFamily: 'Montserrat', color: '#4B4C5D', margin: '3rem' }}>


                        We are also going to start providing energy efficiency solutions and electrical turnkey contracts very soon.
                        Our clientele include Kapoor Industries - Panipat, Tynor Orthodontics - Mohali,  EM Foods - Panipat, Piet Engineering College Panipat and many more.
                    </Typography>
                    <Button
                        size='medium'
                        onClick={handleOpen}
                        style={{
                            background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                            border: 0,
                            borderRadius: 3,
                            boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                            color: 'white',
                            height: 48,
                            padding: '0 30px',
                            margin: '3rem',
                        }}

                    >
                        Contact Us
                    </Button>

                </Grid>

                <Grid item xs={6}>
                    <img src={about} alt="" style={{ maxHeight: '23rem', margin: '3rem' }} />
                </Grid>

                <Grid item xs={6}>
                    <AboutContainer>
                        <Circle>

                        </Circle>
                        <Typography variant='h6'>
                            Ashish Burman
                        </Typography>
                        <Typography variant='body1'>
                            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Eius commodi amet at ipsa officiis alias error nesciunt nemo ullam vitae cumque delectus laboriosam, distinctio, est voluptate sunt debitis asperiores culpa.
                        </Typography>
                    </AboutContainer>

                </Grid>


                <Grid item xs={6}>
                    <AboutContainer>
                        <Circle>

                        </Circle>
                        <Typography variant='h6'>
                            Mukul Agrawal
                        </Typography>
                        <Typography variant='body1'>
                            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Eius commodi amet at ipsa officiis alias error nesciunt nemo ullam vitae cumque delectus laboriosam, distinctio, est voluptate sunt debitis asperiores culpa.
                        </Typography>
                    </AboutContainer>
                </Grid>

                <Grid item xs={12}>
                    <Footer />
                </Grid>


            </Grid>


            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <form style={{ margin: '1rem', height: '30rem', width: '30rem' }}>

                    <DialogTitle>
                        <Typography variant='h5' style={{ fontFamily: 'Montserrat', color: '#4B4C5D' }}>
                            Contact Us
                        </Typography>
                    </DialogTitle>

                    <DialogContent style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', alignItems: 'center', height: '25rem' }}>

                        <TextField
                            variant='outlined'
                            label='Name'
                            id='name'
                            margin='dense'
                            type='string'
                            placeholder='Enter your name'
                            fullWidth
                            onChange={handleNameChange}
                            size='Normal'
                        >


                        </TextField>

                        <TextField
                            variant='outlined'
                            label='Email'
                            id='email'
                            margin='dense'
                            type='string'
                            placeholder='Enter your email'
                            fullWidth
                            size='Normal'
                            onChange={handleEmailChange}>

                        </TextField>

                        <TextField
                            variant='outlined'
                            label='Contact Number'
                            margin='dense'
                            id='number'
                            type='string'
                            placeholder='Enter your contact number'
                            fullWidth
                            onChange={handleNumberChange}>

                        </TextField>
                        <TextField
                            multiline
                            rows={5}
                            margin='dense'
                            variant='outlined'
                            label='Message'
                            id='message'
                            type='string'
                            fullWidth
                            onChange={handleMessageChange}>

                        </TextField>
                        <Button style={{

                            background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                            border: 0,
                            borderRadius: 3,
                            boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                            color: 'white',
                            height: 48,
                            width: '10rem',
                            padding: '0 30px',

                        }}
                            variant='contained'
                            onClick={handleSubmit}
                        >
                            Submit
                        </Button>

                    </DialogContent>

                </form>

            </Dialog>

        </Container>
    )
}
